.findChar {
	margin-top: 35px;
	padding: 25px;
	box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
	position: relative;
	z-index: 5;
	background-color: #fff;
	grid-column-start: 2;
	display: grid;
	grid-template-columns: 250px 125px;
	justify-content: space-between;
	&__header {
		font-weight: bold;
		font-size: 18px;
		line-height: 24px;
		grid-column-start: 1;
		grid-column-end: 3;
	}
	form {
		margin-top: 15px;
		width: 375px;
		grid-row-start: 2;
		grid-column-start: 1;
		grid-column-end: 3;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		input {
			padding: 10px;
			width: 250px;
		}
	}
	&_finded {
		margin-top: 15px;
		font-weight: bold;
		font-size: 18px;
		line-height: 24px;
		color: green;
	}
	&_notFinded {
		margin-top: 10px;
		font-weight: bold;
		font-size: 18px;
		line-height: 24px;
		color: #9F0013;
		grid-column: 1 / 3;
	}
	a.button {
		margin-top: 15px;
		margin-left: 24px;
	}
}